body{
  margin-top: 80px;
}


// Default navbar
.navbar-default {
  background-color: transparent;
  border: 0;

  .navbar-brand {
    color: #ffffff;
    &:hover,
    &:focus {
      color: #ffffff;
    }
  }

  .navbar-text {
    color: red;
  }

  .navbar-nav {
    > li{
      // Active state, and its :hover to override normal :hover
      &.active > a {
        &,
        &:hover,
        &:focus {
          color: #ffffff;
          background-color: transparent;
          cursor: default;
        }
      }

      //Border bottom effect
      &:after {
        content: '';
        display: block;
        margin: auto;
        height: 4px;
        width: 0px;
        background: transparent;
      }

      &.active:after,
      &:hover:after,
      &:focus:after {
        width: 100%;
        background: #ffffff;
      }

      > a {
        color: rgba(255, 255, 255, .8);

        &:hover,
        &:focus {
          color: #ffffff;
          background: rgba(255, 255, 255, .2);

        }
      }
      > .active > a {
        &,
        &:hover,
        &:focus {
          color: #ffffff;
          background-color: none;
        }
      }
      > .disabled > a {
        &,
        &:hover,
        &:focus {
          color: #666666;
        }
      }
    }
    }
}
